@keyframes animation-1 {
   0%   {color: red; transform: rotate(0deg);}
   25%  {color: orange;transform: rotate(15deg);}
   50%  {color: yellow; transform: rotate(0deg);}
   75%  {color: blue;transform: rotate(-15deg);}
   100% {color: red;transform: rotate(0deg);}
}

@keyframes animation-2 {
   0%   {color: red; transform: rotate(0deg);}
   25%  {color: orange;transform: rotate(2deg);}
   50%  {color: yellow; transform: rotate(0deg);}
   75%  {color: blue;transform: rotate(-2deg);}
   100% {color: red;transform: rotate(0deg);}
}

.main {
   position: absolute;
   width: fit-content; height: fit-content;
   margin: auto;
   top: 0; bottom: 20%; left: 0; right: 0;

   font-size: 80px;
   text-align: center;

   animation-name: animation-1;
   animation-timing-function: linear;
   animation-duration: 25s;
   animation-iteration-count: infinite;
}

.main-sub {
   position: absolute;
   width: fit-content; height: fit-content;
   margin: auto;
   top: 50%; bottom: 0; left: 0; right: 0;

   font-size: 60px;

   animation-name: animation-2;
   animation-timing-function: linear;
   animation-duration: 25s;
   animation-iteration-count: infinite;
}


.thanks {
   text-align: center;
   position: absolute;
   right: 1%;
   bottom: 1%;
}

.creation {
   text-align: center;
   position: absolute;
   left: 1%;
   bottom: 1%;
}

.small-text {
   font-size: 12px;
}

@keyframes back {
   0%   {background-position: 0% 82%;}
   50%   {background-position: 100% 19%;}
   100%   {background-position: 0% 82%;}
}

.background {
   width: 100vw; height: 100vh;

   background-image: linear-gradient(rgba(255, 25, 33, 0.1), rgba(255, 25, 117, 0.1), rgba(255, 124, 173, 0.1));
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-size: 1800% 1800%;


   animation-name: back;
   animation-timing-function: linear;
   animation-duration: 25s;
   animation-iteration-count: infinite;
}

.small-thanks {
   display: none;
}

@media only screen and (max-width: 1000px) {
   .main {
      font-size: 55px;
   }
   .main-sub {
      font-size: 45px;
   }
   
   .creation {
      display: none;
   }
   .thanks {
      display: none;
   }
   
   .small-thanks{
      display: block;
      text-align: center;
      position: absolute;
      left: 0; right: 0;
      margin: auto;
      width: fit-content;
      bottom: 1%;
   }
}